<template>
  <div>
    <comp-header></comp-header>
    <el-row align="middle" class="sign-content" justify="center" type="flex">
      <el-col :sm="{ span: 6 }" :xs="{ span: 22 }">
        <el-form :model="form" ref="form">
            <div style="padding-bottom:30px;display:flex;flex-direction:row;justify-content:center;align-items:center;"><span style="color:#666">当前服务端:</span><el-tag style="margin-left:10px;">{{domain}}</el-tag><el-button type="text" @click="$router.push({name: 'config'})" style="margin-left:10px;" size="small">切换</el-button></div>
          <el-form-item label="手机号">
            <el-input placeholder="输入您的手机号" v-model="form.mobile">
              <span @click="getValidCode" class="sign-verify_code" slot="suffix" v-show="authcodeStatus === 0">{{ countDown > 0 ? countDown : '获取验证码' }}</span>
              <i class="el-icon-loading" slot="suffix" v-show="authcodeStatus === 1"></i>
            </el-input>
          </el-form-item>
          <el-form-item label="短信验证码">
            <el-input placeholder="输入收到的短信验证码" v-model="form.code"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button ref="signinButton" :disabled="disableButton" @click="onSubmit" style="width: 100%;" type="primary" :loading="singinLoading">登录</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
/* eslint-disable */
import { rpc1 } from '../service/index';
import { M } from '../service/grpc';
import { getToken, setToken } from '../service/helper';
import { setTimeout, clearTimeout } from 'timers';
import Header from '../components/layout/header.vue';

export default {
  components: {
    'comp-header': Header,
  },
  data() {
    return {
      domain: localStorage.getItem('domain'),
      form: {
        mobile: '',
        code: ''
      },
      authcodeStatus: 0, // 0 正常状态  1 正在请求发送验证码
      countDown: 0,
      singinLoading: false
    };
  },
  computed: {
    disableButton() {
      const { code, mobile } = this.form;
      return !code || !mobile;
    }
  },
  created() {
    if (getToken()) {
      this.$router.push({ name: 'dashboard' });
    }
  },
  methods: {
    stopCountDown() {
      clearTimeout(this.timer);
      this.timer = null;
    },
    startCountDown() {
      if (this.countDown === 0) return this.stopCountDown();

      this.countDown = this.countDown - 1;
      this.timer = setTimeout(this.startCountDown, 1000);
    },
    validMobile() {
      return this.form.mobile.length === 11;
    },
    validAuthCode() {
      return /^\d{4}$/.test(this.form.code);
    },
    errorMessage(message) {
      this.$message({ message, type: 'error' });
    },
    onSubmit() {
      if (!this.validMobile()) return this.errorMessage('请输入正确的手机号码');
      if (!this.validAuthCode()) return this.errorMessage('请输入正确的验证码');
      this.singinLoading = true;

      rpc1(
        'StoreAuthService.Auth',
        M('MobileAuthRequest', {
          token: M('Token', { token: this.token }),
          code: this.form.code,
          mobile: this.form.mobile
        })
      )
        .then(res => {
          setToken(res.token);
          this.singinLoading = false;
          this.$router.push({ name: 'home' });
        })
        .catch(e => {
          this.singinLoading = false;
        });
    },
    getValidCode() {
      if (!this.validMobile()) return this.errorMessage('请输入正确的手机号码');

      this.authcodeStatus = 1;
      rpc1('StoreAuthService.SigninSendCode', M('CodeRequest', { mobile: this.form.mobile }))
        .then(res => {
          this.token = res.token;
          this.countDown = 60;
          this.startCountDown();
          this.authcodeStatus = 0;
        })
        .catch(e => {
          this.authcodeStatus = 0;
        });
    }
  }
};
</script>

<style lang="scss">
.sign-content {
  height: calc(100vh - 80px);
  background-color: white;
}
.el-input--suffix .el-input__inner {
  padding-right: 90px;
}
.sign-verify_code {
  padding-right: 5px;
  font-size: 12px;
  cursor: pointer;
  color: #888;
}
</style>
